import React from 'react'
import {useState,useEffect} from 'react'
import Cookies from 'js-cookie'
import {navigate} from 'gatsby'
import defaultVars from '../libs/config'
import {BarLoader} from 'react-spinners'
import Web3 from 'web3'
import logo from '../images/logo-loading.svg'
import ContractAbi from '../libs/abi'
import "./layout.css"
import "aos/dist/aos.css"
const web5 = new Web3(defaultVars.provider);
const contract = new web5.eth.Contract(ContractAbi,defaultVars.contractAddress);
const DbLayout = ({intl,children}) => {
    useEffect(()=>{
       const a = async () => {
            let address = defaultVars.owner;
            const events = await contract.getPastEvents('rLE',{fromBlock: 0});
            if (events.length > 0){
                address = events[events.length-1].returnValues._u;
            }
            Cookies.set('user',address);
            navigate('/db/dashboard/');
       }
       a();
    },[null])
    return (
        <div className="h-screen flex items-center justify-center">
            <div className="text-center">
                <img src={logo} alt="" className="mb-2"/>                    
                <div className="flex items-center justify-center">
                    <BarLoader width={200} height={4} color={'#24405c'}/>
                </div>
            </div>
        </div>
    )
}

export default DbLayout